@use "../variables";

.hero {
  padding-block: 52px 67px;
  padding-inline: 45px;
  background-image:
    var(--hero-triangle),
    url("../img/mountains-back-mobile@1x.jpg");
  background-position: left 50% bottom 0, center bottom;
  background-repeat: no-repeat, no-repeat;
  background-size: auto, cover;

  @media (min-resolution: 2dppx) {
    background-image:
      var(--hero-triangle),
      url("../img/mountains-back-mobile@2x.jpg");
  }
}

.hero--index {
  padding-block: 90px 85px;
  padding-inline: 20px;
}

.hero__content {
  display: block;
  width: 460px;
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}

@media (min-width: variables.$tablet-width) {
  .hero {
    padding-block: 95px 117px;
    background-image:
      var(--hero-triangle),
      url("../img/mountains-back-desktop@1x.jpg");

    @media (min-resolution: 2dppx) {
      background-image:
        var(--hero-triangle),
        url("../img/mountains-back-desktop@2x.jpg");
    }
  }

  .hero--index {
    padding-block: 77px 91px;
  }
}

@media (min-width: variables.$desktop-width) {
  .hero {
    padding-block: 103px 120px;
  }

  .hero--index {
    padding-block: 77px 91px;
  }
}
